.app {
  font-family: Montserrat, sans-serif;
  height: "100%";
}

.dropdown:hover .dropdown-menu {
  display: block;
}
.dropdown-toggle::after {
  content: none !important;
}

/* .app-footer {

} */

.body {
  text-align: center;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
}

.body-members {
  text-align: center;
  flex-direction: row;
  flex-wrap: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
}

.content-members {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.page {
  position: relative;
  width: 100%;
  height: 100vh;
}

.title {
  color: white;
  font-size: 2em;
  font-weight: 700;
  text-align: center;
  position: absolute;
  width: 100%;
  top: 10vh;
}

.content {
  width: 100%;
  height: 80vh;
  top: 20vh;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.about-content {
  width: 80%;
  height: 50vh;
}

.mission-text-hidden {
  visibility: hidden;
}

.navbar__link--active {
  border-bottom: 3px solid white;
  transition: border-bottom 0.5s ease-in-out;
}

.catalyst-logo-text {
  position: relative;
  text-align: center;
  width: 100%;
}

.arrow-down {
  width: 100%;
  position: absolute;
  bottom: 10vh;
}

.arrow-down::after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 1;
  border-right: 4px solid white;
  border-bottom: 4px solid white;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  animation: 3s arrow infinite ease;
  -webkit-animation: 3s arrow infinite ease;
}

.faq-line {
  display: "flex";
  flex-direction: "row";
  margin-left: "30px";
  margin-right: "30px";
}

.members-selected {
  font-weight: 700;
}

.members-unselected {
  font-weight: 400;
}

.img-work {
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 5%;
  margin-right: 5%;
}

.link-icon {
  margin: 1vh;
}

@-webkit-keyframes arrow {
  0%,
  100% {
    top: 50px;
  }
  50% {
    top: 80px;
  }
}

@keyframes arrow {
  0%,
  100% {
    top: 50px;
  }
  50% {
    top: 80px;
  }
}

.heart {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: url(https://cssanimation.rocks/images/posts/steps/heart.png)
    no-repeat;
  background-position: 0 0;
  cursor: pointer;
  animation: fave-heart 1s steps(28);
}
.heart:hover {
  background-position: -2800px 0;
  transition: background 1s steps(28);
}
@keyframes fave-heart {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -2800px 0;
  }
}
